export default {
  data() {
    return {
      silderMenu: [
//      {path: '/CityProject', label: '项目',icon: '<i class="el-icon-circle-plus"/>'},
//      {path: '/UserAuth', label: '账号权限'},
//      {path: 'c', label: '数据收集'},
//      {path: 'c', label: '审计记录'},
//      {path: '/ProgramFlow', label: '程序流程'},
//      {path: '/CountyProject', label: '区县项目列表'},
//      {path: '/CityProject', label: '项目',icon: '<i class="el-icon-circle-plus"/>'},
//      {path: '/UserAuth', label: '账号权限'},
//      {path: '/CityAssignAccount', label: '市局科室账号分配'},
//      {path: '/DataCollection', label: '数据收集'},
//      {path: '/Record', label: '审计记录'},
//      {path: '/ProgramFlow', label: '程序流程'},
//      {path: '/CountyProject', label: '区县项目列表'},
      ],
    }
  },

  mounted() {
    this.$nextTick(() => {
      // this.$refs.frame.closeLoading();
      this.queryInit();
    })
  },

  methods: {
  	queryInit(){
				var temp = this;
				this.$post('/web/queryUserMenu').then(function(m) {
					temp.silderMenu=m.content.list;
					temp.$router.push({path: m.content.murl})
				})
		},

  }
}



